* {
	margin: 0;
	padding: 0;
}

.BottomHeader {
	font-size: 1.7fw;
	width: 100%;
	height: 5.3vw;
	display: flex;
	flex-wrap: wrap;
}

.ulBottom {
	list-style: none;
}

.ul {
	list-style: none;
}
.blocktitleBottomContent {
	margin-left: 5%;
	margin-top: 2%;
	width: 90%;
	height: 80%;
}
.blocktitleBottomContentUl {
	margin-left: 5%;
	margin-top: 2%;
	width: 90%;
	height: 80%;
}
.blocktitleBottom {
	position: relative;
	width: 25%;
	height: auto;
	object-fit: contain;
	box-sizing: border-box;
	position: relative;
	border: 0.25px solid #000000cc;
}
.blocktitleBottomUl {
	position: relative;
	width: 25%;
	height: auto;
	object-fit: contain;
	box-sizing: border-box;
	position: relative;
	border: 0.25px solid #000000cc;
}
.block {
	width: 25%;
	height: auto;
	object-fit: contain;
	box-sizing: border-box;
	position: relative;
}

.input {
	width: 100%;
	border: 0;
	border-bottom: 1px solid black;
	padding: 0;
	margin: 0;
}

.halfblockdiagBottom {
	justify-content: center;
	display: flex;
	align-items: center;
	object-fit: contain;
	box-sizing: border-box;
	position: relative;
	justify-content: center;
	display: flex;
	width: 100%;
	height: 100%;
	object-fit: contain;
	box-sizing: border-box;
	position: relative;
	border: 0.25px solid #000000cc;
	border-bottom: 0.5px solid #000000cc;
}

.blocktitleBottomInput {
	position: relative;
	width: 25%;
	box-sizing: border-box;
	border: 0.25px solid #000000cc;
}

.diagBottom {
	width: 100%;
	height: 100%;
	background: linear-gradient(
			to bottom right,
			transparent calc(50% - 1px),
			black calc(50% - 1px),
			black 50%,
			transparent 50%
		),
		linear-gradient(to bottom left, transparent calc(50% - 1px), black calc(50% - 1px), black 50%, transparent 50%);
}

.pagetitle {
	margin-left: 0;
}
.amount {
	right: 0;
}
.pageslist {
	padding-left: 13vw;
	gap: 13vw;
	display: flex;
	list-style: none;
}

@media (max-width: 1200px) {
	.blocktitleBottomUl {
		position: relative;
	}
	.BottomHeader {
		font-size: 1.5vw;
		height: 7vw;
	}

	.halfblockdiagBottom {
		position: relative;
	}
	.BottomHeader {
		height: 9vw;
	}
	.blocktitleBottomInput {
		position: relative;
	}
}

@media (min-width: 1540px) {
	.BottomUl {
		font-size: 1.6vw;
	}
	.blocktitleBottomContent {
	}
}
